import React, { useState } from "react";
import { dbService, authService } from 'fbase';
import { collection, addDoc } from "firebase/firestore";

const HomeworkSubmit = ({ match, userObj }) => {
    const [submit, setSubmit] = useState("");

    const HomeworkID = match.params.id;
    const UID = authService.currentUser.uid;

    const onSubmit = async (event) => {
        event.preventDefault();
        try {
            const data = {
                text: submit,
                data: Date.now(),
                uid: UID
            }
            const loadCollection = collection(dbService, "homework", HomeworkID, "submit");

            //ÏàôÏ†ú ÎÇ¥Ïö© DBÏóê ÏûëÏÑ±
            const docRef = await addDoc(loadCollection, data);
            console.log("Document written with ID: ", docRef.id);
        } catch (e) {
            console.error("Error adding document: ", e);
        }
        setSubmit("");
    }

    const onChange = (event) => {
        const { target: { value }, } = event;
        setSubmit(value);
    };

    return (
        <>
            {console.log(match.params.id)}
            <form className="HomeworkSubmitTextArea" onSubmit={onSubmit}>
                <textarea
                    className="HomeworkSubmitTextArea__textarea"
                    value={submit}
                    onChange={onChange}
                    type="text"
                    placeholder="ÏàôÏ†ú ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî."
                    autoFocus
                />
                <input className="HomeworkSubmitTextArea__button" type="submit" value="Ï†úÏ∂ú" />
            </form>
        </>
    )
}

export default HomeworkSubmit;